import React from "react"

import Layout from "../components/layout"
import SEO from "../components/seo"
import Image from 'gatsby-image'


export default ({data}) => {

    return (
    <Layout heading = 'RESUME'  >
       <SEO title = 'resume'/>
       <Image style = {{maxWidth:'800px' , margin:'0 auto'}} fluid = {data.file.childImageSharp.fluid} />
       <a style = {{ display:'block',
                     textAlign:'center',
                     margin:'30px 0px',
                     fontSize:'24px',
                     color:'#2851A3'
                     }}
          href='../resume/Anton_Savytskyi_Resume.pdf' download >Download</a>
    </Layout>
    
)}

export const pageQuery = graphql`
  query ResumeQuery{
    file(relativePath: { eq: "resume.jpg" }) {
      childImageSharp {
        fluid(maxWidth: 2000, quality: 100){
          ...GatsbyImageSharpFluid_tracedSVG
        }
      }
    }
  }`